.navbar {
background-color: white; 
height: 10vh;
display: flex;
flex-direction: row; 
justify-content: space-between; 
}

.navstart {
display: flex;
justify-content: flex-start;
align-items: center;
}

.navend {
display: flex;
text-align: center;
align-items: center;
justify-content: flex-end;
}

.staroutline {
max-width: 30%; 
max-height: 30%;
padding-bottom: 0.6vh;
padding-left: 4vh;
list-style-type: none;
text-decoration: none;
}

.navheaders {
display: flex;
flex-direction: row;
width: 15vh;
justify-content: space-between;
list-style-type: none;
padding-right: 2vh;
}

.headers {
font-family: "Inter-Regular";
color: #666666;
padding-bottom: 0.6vh;
text-decoration: none;
}

.menu {
background-color: white;
border-width: 0px;
width: 10vh;
max-width: 100%;
max-height: 100%;
height: 10vh;
}

.menuicon {
max-width: 30%;
max-height: 30%;
}